      rich: {
        v: { fontSize: 15, color: 'white' },
        unit: { fontSize: 10, color: 'white'  },
        // t: { fontSize: 10, color: 'white'  }
      }
    },
    // left: '4%',
    // right: '4%',
    // bottom: '10%',
    top: '40%',
    
