      radius: '63%', // 
      startAngle: 0,
      endAngle: 360,
      splitNumber: 12,
      axisLine: { show: false },
      splitLine: {
        // length: 39,
        length: '24%',
        lineStyle: {
          width: 10,
          color: '#3c4ffc44'
        }
      },
      axisTick: { show: false },
      axisLabel: { show: false }
    }
  ]
};

setInterval(() => {
  angle = angle + 2;
  myChart.setOption(option, true);
}, 100);

option && myChart.setOption(option);

}
}
</script>