<template>
  <div id="dsrd" style="width: 100%; height: 100%;"></div>
</template>
<script>
import * as echarts from 'echarts';
import 'echarts-gl';


export default {
  mounted() {
    var chartDom = document.getElementById('dsrd');
    var myChart = echarts.init(chartDom);
    var option;
    var height = 40;//

    let hoveredIndex = '';
    option = getPie3D(
        [
            {
                name: "CVE-123",
                value: 29,
                itemStyle:{
                    color:'#ffa7fc',
                }
            },
            {
              name:'others',
              value: 71,
              itemStyle:{
                  color:'#b337fc',
              }
            }
        ],
        0.60
    );
    // 
    function getParametricEquation(startRatio, endRatio, isSelected, isHovered, k, h) {
        // 
        const midRatio = (startRatio + endRatio) / 2;

        const startRadian = startRatio * Math.PI * 2;
        const endRadian = endRatio * Math.PI * 2;
        const midRadian = midRatio * Math.PI * 2;

        // 
        if (startRatio === 0 && endRatio === 1) {
            // eslint-disable-next-line no-param-reassign
            isSelected = false;
        }

        // / k 1/3
        // eslint-disable-next-line no-param-reassign
        k = typeof k !== 'undefined' ? k : 1 / 3;

        //  x y  0
        const offsetX = isSelected ? Math.cos(midRadian) * 0.1 : 0;
        const offsetY = isSelected ? Math.sin(midRadian) * 0.1 : 0;

        //  1
        const hoverRate = isHovered ? 1.05 : 1;

        // 
        return {
            u: {
                min: -Math.PI,
                max: Math.PI * 3,
                step: Math.PI / 32,
            },

            v: {
                min: 0,
                max: Math.PI * 2,
                step: Math.PI / 20,
            },

            x(u, v) {
                if (u < startRadian) {
                    return offsetX + Math.cos(startRadian) * (1 + Math.cos(v) * k) * hoverRate;
                }
                if (u > endRadian) {
                    return offsetX + Math.cos(endRadian) * (1 + Math.cos(v) * k) * hoverRate;
                }
                return offsetX + Math.cos(u) * (1 + Math.cos(v) * k) * hoverRate;
            },

            y(u, v) {
                if (u < startRadian) {
                    return offsetY + Math.sin(startRadian) * (1 + Math.cos(v) * k) * hoverRate;
                }
                if (u > endRadian) {
                    return offsetY + Math.sin(endRadian) * (1 + Math.cos(v) * k) * hoverRate;
                }
                return offsetY + Math.sin(u) * (1 + Math.cos(v) * k) * hoverRate;
            },

            z(u, v) {
                if (u < -Math.PI * 0.5) {
                    return Math.sin(u);
                }
                if (u > Math.PI * 2.5) {
                    return Math.sin(u) * h * 0.1;
                }
                // Zhvalue
                return Math.sin(v) > 0 ? 1 * h * 0.1 : -1;
            },
        };
    }
    //  3D 
    function getPie3D(pieData, internalDiameterRatio) {
        const series = [];
        // 
        let sumValue = 0;
        let startValue = 0;
        let endValue = 0;
        const legendData = [];
        const k =
            typeof internalDiameterRatio !== 'undefined'
                ? (1 - internalDiameterRatio) / (1 + internalDiameterRatio)
                : 1 / 3;

        //  series-surface 
        for (let i = 0; i < pieData.length; i += 1) {
            sumValue += pieData[i].value;
            const seriesItem = {
                name: typeof pieData[i].name === 'undefined' ? `series${i}` : pieData[i].name,
                type: 'surface',
                parametric: true,
                wireframe: {
                    show: false,
                },
                pieData: pieData[i],
                pieStatus: {
                    selected: false,
                    hovered: false,
                    k,
                },
            };

            if (typeof pieData[i].itemStyle !== 'undefined') {
                const { itemStyle } = pieData[i];

                // eslint-disable-next-line no-unused-expressions
                typeof pieData[i].itemStyle.color !== 'undefined' ? (itemStyle.color = pieData[i].itemStyle.color) : null;
                // eslint-disable-next-line no-unused-expressions
                typeof pieData[i].itemStyle.opacity !== 'undefined'
                    ? (itemStyle.opacity = pieData[i].itemStyle.opacity)
                    : null;

                seriesItem.itemStyle = itemStyle;
            }
            series.push(seriesItem);
        }
        //  sumValue getParametricEquation 
        //  series-surface  series-surface.parametricEquation
        console.log(series);
        for (let i = 0; i < series.length; i += 1) {
            endValue = startValue + series[i].pieData.value;

            series[i].pieData.startRatio = startValue / sumValue;
            series[i].pieData.endRatio = endValue / sumValue;
            series[i].parametricEquation = getParametricEquation(
                series[i].pieData.startRatio,
                series[i].pieData.endRatio,
                false,
                false,
                k,
                //
                height
            );

            startValue = endValue;

            legendData.push(series[i].name);
        }

        //  legendDataseries 
        const option = {
            // animation: false,
            tooltip: {
                formatter: (params) => {
                    if (params.seriesName !== 'mouseoutSeries') {
                        return `${
                            params.seriesName
                        }<br/><span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color: :${
                            params.color
                        };"></span>${option.series[params.seriesIndex].pieData.value}`;
                    }
                    return '';
                },
            },
            xAxis3D: {
                min: -1,
                max: 1,
            },
            yAxis3D: {
                min: -1,
                max: 1,
            },
            zAxis3D: {
                min: -1,
                max: 1,
            },
            grid3D: {
                show: false,
                boxHeight: 5,
                top: '5%',
                viewControl: {
                    // 3d
                    alpha: 22,
                    // beta: 30,
                    rotateSensitivity: 1,
                    zoomSensitivity: 0,
                    panSensitivity: 0,
                    autoRotate: true,
                    distance: 150,
                },
            },
            series,
        };
        return option;
    }
    //   bug
    //  mouseover
    myChart.on('mouseover', function (params) {
        // 
        let isSelected;
        let isHovered;
        let startRatio;
        let endRatio;
        let k;
        let i;

        //  mouseover 
        if (hoveredIndex === params.seriesIndex) {
            return;

            // 
        } else {
            //  option 
            if (hoveredIndex !== '') {
                //  option.series  false
                isSelected = option.series[hoveredIndex].pieStatus.selected;
                isHovered = false;
                startRatio = option.series[hoveredIndex].pieData.startRatio;
                endRatio = option.series[hoveredIndex].pieData.endRatio;
                k = option.series[hoveredIndex].pieStatus.k;
                i = height
                //  option 
                option.series[hoveredIndex].parametricEquation = getParametricEquation(
                    startRatio,
                    endRatio,
                    isSelected,
                    isHovered,
                    k,
                    i
                );
                option.series[hoveredIndex].pieStatus.hovered = isHovered;

                //  seriesIndex 
                hoveredIndex = '';
            }

            //  mouseover  option 
            if (params.seriesName !== 'mouseoutSeries') {
                //  option.series  true
                isSelected = option.series[params.seriesIndex].pieStatus.selected;
                isHovered = true;
                startRatio = option.series[params.seriesIndex].pieData.startRatio;
                endRatio = option.series[params.seriesIndex].pieData.endRatio;
                k = option.series[params.seriesIndex].pieStatus.k;

                //  option 
                option.series[params.seriesIndex].parametricEquation = getParametricEquation(
                    startRatio,
                    endRatio,
                    isSelected,
                    isHovered,
                    k,
                    (option.series[params.seriesIndex].pieData.value)/2 + height
                );
                option.series[params.seriesIndex].pieStatus.hovered = isHovered;

                //  seriesIndex
                hoveredIndex = params.seriesIndex;
            }

            //  option
            myChart.setOption(option);
        }
    });

    //  bug
    myChart.on('globalout', function () {
        // 
        let isSelected;
        let isHovered;
        let startRatio;
        let endRatio;
        let k;
        let i;
        if (hoveredIndex !== '') {
            //  option.series  true
            isSelected = option.series[hoveredIndex].pieStatus.selected;
            isHovered = false;
            k = option.series[hoveredIndex].pieStatus.k;
            startRatio = option.series[hoveredIndex].pieData.startRatio;
            endRatio = option.series[hoveredIndex].pieData.endRatio;
            //  option 
            i = height
            option.series[hoveredIndex].parametricEquation = getParametricEquation(
                startRatio,
                endRatio,
                isSelected,
                isHovered,
                k,
                i
            );
            option.series[hoveredIndex].pieStatus.hovered = isHovered;

            //  seriesIndex 
            hoveredIndex = '';
        }

        //  option
        myChart.setOption(option);
    });
    option && myChart.setOption(option);
  }
}
</script>